.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }

  to {
    transform: rotate(360deg);
  }
}

html,
body {
  height: 100%;
  font-family: 'Manrope';
  font-size: 16px;
}

.sidebar {
  box-shadow: 4px 0px 16px rgba(30, 30, 30, 0.08);
  position: fixed;
  background: #FFFFFF;
}

.dropdown-btn {
  color: #000000;
  text-decoration: none;
  display: block;
  border: none;
  background: none;
  width: 100%;
  cursor: pointer;
  outline: none;
  margin-bottom: 20px;
}

.dropdown-container {
  border-left: 1px solid #C0C0C0;
  padding-left: 20px;
  display: block;
  font-size: 14px;
  margin-left: 5px;
  margin-top: 5px;
}

.fa-angle-down,
.fa-angle-up {
  float: right;
}

a:link,
a:visited,
a:hover,
a:active {
  text-decoration: none;
  color: #000000;
}

.active {
  font-weight: bold;
}

.unread {
  width: 57px;
  height: 16px;
  float: right;
  background: #FF7474;
  color: #FFFFFF;
  border-radius: 22px;
  font-size: 10px;
  text-align: center;
}

.navbar {
  color: #C0C0C0;
  box-shadow: 4px 0px 16px 10px rgba(30, 30, 30, 0.08);
  position: fixed;
  margin-left: 240px;
  padding-left: 15px;
  width: 100%;
  background: #FFFFFF;
}

.rounded-circle {
  background-color: #F44336;
}

.main-content {
  margin-top: 50px;
  margin-left: 240px;
}

.similar-accounts-heading {
  margin-top: 110px;
  margin-left: 60px;
  font-size: 22px;
  color: #808080;
}

.row {
  display: flex;
  float: left;
}

.column {
  flex: 50%;
  max-width: 430px;
}

.company-img {
  height: 50px;
  width: 50px;
  margin-left: 60px;
  float: left;
  margin-right: 20px;
}

p .company-title {
  float: right;
}

.company-title {
  font-weight: bold;
  font-size: 16px;
}

.company-text {
  margin-top: -15px;
  font-size: 12px;
  color: #808080;
}

.quick-actions {
  float: right;
  margin-top: -35px;
  margin-left: 900px;
  position: absolute;
}

.quick-actions-heading {
  font-size: 22px;
  color: #808080;
}

.quick-actions-text p {
  font-size: 14px;
  font-weight: bold;
  margin-top: 30px;
}

.searchbox {
  border: none;
  width: 266px;
  font-size: 16px;
}

.searchbox:focus {
  outline: none;
  background: #F5F5F5;
  font-size: 16px;
  font-weight: 700;
}

.searchbox:focus::placeholder {
  color: #D3D3D3;
}